import { useState } from "react";
import { DisplayWrapper } from "../index.styled";
import {
  Container,
  List,
  Wrapper,
  Row,
  SvgWrapper,
  ButtonWrapper,
  ListItem,
} from "./PlasticAcceptance.styled";
import { Heading } from "./../../ui/text/text.styled";
import {
  BackArrow,
  BottleFour,
  BottleOne,
  BottleThree,
  BottleTwo,
  UpMark,
} from "../../ui/icons/Icons";
import { Button } from "../../ui/button/Button.styled";
import { Transition } from "react-transition-group";
import { Modal } from "../../ui/modal/Modal";

export const PlasticAcceptance = ({ animation }) => {
  const [step, setStep] = useState(1);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleOpen = () => {
    setIsModalOpen(true);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const handleSetNextStep = () => setStep(step + 1);
  const handleSetPrevStep = () => setStep(step - 1);

  return (
    <DisplayWrapper>
      {step === 1 && (
        <Transition in={animation && step === 1} timeout={200}>
          {(state) => (
            <Wrapper>
              <Row content={"start"} state={state}>
                <Container>
                  <Heading>   ?</Heading>
                  <List>
                          
                         . ,
                     10%     
                     3%.  ,   
                    ,    ,   
                       30%.
                    <br />
                      ,   
                        ,   .
                  </List>
                </Container>
                <ButtonWrapper buttonWidth="300px">
                  <Button font="20px" onClick={handleSetNextStep}>
                     ?
                  </Button>
                </ButtonWrapper>
              </Row>
            </Wrapper>
          )}
        </Transition>
      )}
      {step === 2 && (
        <Transition in={animation && step === 2} timeout={200}>
          {(state) => (
            <Wrapper>
              <Row state={state}>
                <Container>
                  <Heading></Heading>
                  <List>
                    <ListItem green>
                      <UpMark />
                      <p>
                        -  5  (, ,
                        ,    ,  
                         ,     
                        )
                      </p>
                    </ListItem>
                    <ListItem green>
                      <UpMark />
                      <p>
                        , ,    , ,
                            ..
                      </p>
                    </ListItem>
                    <ListItem green>
                      <UpMark />
                      <p>-,  ,  </p>
                    </ListItem>
                    <ListItem green>
                      <UpMark />
                      <p>  19  </p>
                    </ListItem>
                    <ListItem green>
                      <UpMark />
                      <p>   </p>
                    </ListItem>
                  </List>
                </Container>
                <Container>
                  <Heading> </Heading>
                  <List>
                    <ListItem red>
                      <UpMark />
                      <p>   </p>
                    </ListItem>
                    <ListItem red>
                      <UpMark />
                      <p></p>
                    </ListItem>
                    <ListItem red>
                      <UpMark />
                      <p></p>
                    </ListItem>
                    <ListItem red>
                      <UpMark />
                      <p>    4, 5, 6, 7, 8</p>
                    </ListItem>
                  <ButtonWrapper buttonWidth="500px">
                    <Button
                      font="20px"
                      // onClick={handleSetNextStep}
                      buttonWidth="250px"
                    >
                       ?
                    </Button>
                    <Button
                      font="20px"
                      onClick={handleOpen}
                      buttonWidth="250px"
                    >
                      
                    </Button>
                  </ButtonWrapper>
                  </List>
                </Container>
              </Row>
            </Wrapper>
          )}
        </Transition>
      )}
      <Modal isModalOpen={isModalOpen} handleHide={handleCancel}/>
    </DisplayWrapper>
  );
};
