import { Button } from "../../ui/button/Button.styled";
import { Heading, Text } from "../../ui/text/text.styled";
import {
  MainImage,
  DisplayWrapper,
  Container,
  List,
  GLassImage,
  ListItem,
} from "./MetalReception.styled";
import { useState } from "react";
import metal from "../../../assets/img/metal.png";
import { UpMark } from "../../ui/icons/Icons";
import { Modal } from "../../ui/modal/Modal";

export const MetalReception = () => {
  const [step, setStep] = useState(1);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const handleSetNextStep = () => setStep(step + 1);

  return (
    <DisplayWrapper>
      {step === 1 && (
        <>
          <Container>
            <Heading>    ?</Heading>
            <Text>
                    ,   
              .    ,      
              !
            </Text>
            <List>
                   ,  
                <br />
                  ,    
                  . <br />
            </List>
            <Button buttonWidth="300px" font="20px" onClick={handleSetNextStep}>
               ?
            </Button>
          </Container>
          <GLassImage src={metal} />
        </>
      )}
      {step === 2 && (
        <>
          <Container>
            <Heading></Heading>
            <List>
              <ListItem green>
                <UpMark />
                <p>   </p>
              </ListItem>
              <ListItem green>
                <UpMark /> <p></p>
              </ListItem>
              <ListItem green>
                <UpMark />
                <p>  (  )</p>
              </ListItem>
              <ListItem green>
                <UpMark />{" "}
                <p>
                       (  
                  )
                </p>
              </ListItem>
              <ListItem green>
                <UpMark />{" "}
                <p>,   ..,     </p>
              </ListItem>
              <ListItem green>
                <UpMark />{" "}
                <p>
                      (, , 
                   ..)
                </p>
              </ListItem>
            </List>
          </Container>
          <Container>
            <Heading> </Heading>
            <List>
              <ListItem red>
                <UpMark /> <p>   </p>
              </ListItem>
              <ListItem red>
                <UpMark /> <p> </p>
              </ListItem>
              <ListItem red>
                <UpMark />{" "}
                <p>   (,   ..)</p>
              </ListItem>
              <ListItem red>
                <UpMark /> <p>  (,   ..)</p>
              </ListItem>
            </List>
            <Button buttonWidth="300px" font="20px">
               ?
            </Button>
            <Button buttonWidth="300px" font="20px" onClick={showModal}>
              
            </Button>
            <Modal isModalOpen={isModalOpen} handleHide={handleCancel} />
          </Container>
        </>
      )}
    </DisplayWrapper>
  );
};
