import { Button } from "../../ui/button/Button.styled";
import { Heading, Text } from "../../ui/text/text.styled";
import {
  MainImage,
  DisplayWrapper,
  Container,
  List,
  GLassImage,
  ListItem,
  ButtonsWrapper,
} from "./WastePaper.styled";
import { useState } from "react";
import paper from "../../../assets/img/paper.jpg";
import { UpMark } from "../../ui/icons/Icons";
import { Modal } from "../../ui/modal/Modal";

export const WastePaper = () => {
  const [step, setStep] = useState(1);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const handleSetNextStep = () => setStep(step + 1);

  return (
    <DisplayWrapper>
      {step === 1 && (
        <>
          <Container>
            <Heading>    ?</Heading>
            <Text>
                     
                .     
               ,      
              .
            </Text>
            <List>
               1     4  
              ,  100    1 )  
                .
            </List>
            <Button buttonWidth="300px" font="20px" onClick={handleSetNextStep}>
               ?
            </Button>
          </Container>
        </>
      )}
      {step === 2 && (
        <>
          <Container>
            <Heading></Heading>
            <List>
              <ListItem green>
                <UpMark />
                <p> (  )</p>
              </ListItem>
              <ListItem green>
                <UpMark /> <p>  </p>
              </ListItem>
              <ListItem green>
                <UpMark />
                <p>, </p>
              </ListItem>
              <ListItem green>
                <UpMark /> <p>, , </p>
              </ListItem>
              <ListItem green>
                <UpMark /> <p>, </p>
              </ListItem>
              <ListItem green>
                <UpMark />{" "}
                <p>
                    (,   ..)  
                  
                </p>
              </ListItem>
            </List>
            <Heading> </Heading>
            <List>
              <ListItem red>
                <UpMark />{" "}
                <p>      </p>
              </ListItem>
            </List>
            <ButtonsWrapper>
              <Button buttonWidth="250px" font="20px">
                 ?
              </Button>
              <Button buttonWidth="250px" font="20px" onClick={showModal}>
                
              </Button>
            </ButtonsWrapper>
            <Modal isModalOpen={isModalOpen} handleHide={handleCancel} />
          </Container>
        </>
      )}
      <GLassImage src={paper} />
    </DisplayWrapper>
  );
};
